@media (max-width: 800px) {
  .stateface:before {
      font-family:  StateFaceRegular;
      font-size: 10vw;
  }
}

@media (min-width: 801px) {
  .stateface:before {
      font-family:  StateFaceRegular;
      font-size: 650%;
  }
}

.stateface-replace {
    text-indent: -999em;
    display: inline-block;
    position: relative;
    min-width: 1em;
}

.stateface-replace:before {
    position: absolute;
    left: 0;
    top: 0;
    text-indent: 0;
}

